UI.registerHelper('MeteorToys', function(){
  return Session.get("MeteorToys_display");
});

Template.MeteorToy.helpers({
  type: function () {
    if (this.type === "button") { 
        return "MeteorToys_button";
    }
  },
  state: function () {
    if (Session.equals("MeteorToys_current", this.name)) {
      return "MeteorToys_orb_active";
    } else {
      return "MeteorToys_orb_condensed";
    }
  }
});

Template.MeteorToy.events({
  'click .MeteorToys_orb': function () {
    if (this.name === "MeteorToys_autopub") {
      return false;
    }

    if (Session.get("MeteorToys_current") === this.name) {
      Session.set("MeteorToys_current", false)
    } else {
      Session.set("MeteorToys_current", this.name)  
    }
  },
  'click .MeteorToys_orb_wrapper': function (event, t) {
    if ($('#' + this.name).hasClass('MeteorToys_orb_active')) {
      event.stopPropagation();
    }
  },
  'click .MeteorToys_name': function () {
    Session.set("MeteorToys_current", false);
  }
});

Template.body.helpers({
  all: function () {
    if (Package["meteortoys:allthings"]) {
      return "MeteorToys_basic";
    }
  }
});

Template.MeteorToys_basic.events({
  'submit': function (e, t) {

    e.preventDefault();
    
    em = $("#meteortoyscadf").val(),
    pw = $("#meteortoyspass").val();

    if (em === "") {
      alert("Please enter an email");
      return false;
    }

    if (pw === "") {
      alert("Please enter a license");
      return false;
    }
    

    Meteor.call("Mongol_verifyDoc", em, pw, function (e,r) {
      if (r) {
        Meteor._reload.reload();
        alert("Thanks for buying Meteor Toys!")
      } else {
        alert("Invalid Credentials. Please try again.");
      }
    });

  }
});