MeteorToysData = {
	'Impersonate': new Mongo.Collection("MeteorToys_Impersonate"),
	'JetSetter':   new Mongo.Collection("MeteorToys_JetSetter"),
	'Mongol':      new Mongo.Collection("MeteorToys_Mongol"),
}

MeteorToysData.Impersonate.allow({
	insert: function () {
	    return true;
	},
	remove: function (){
	    return true;
	},
	update: function() {
	    return true;
	}
});

MeteorToysData.JetSetter.allow({
	insert: function () {
	    return true;
	},
	remove: function (){
	    return true;
	},
	update: function() {
	    return true;
	}
});

MeteorToysData.Mongol.allow({
	insert: function () {
	    return true;
	},
	remove: function (){
	    return true;
	},
	update: function() {
	    return true;
	}
});

if (Meteor.isServer) {
	MeteorToysData["credentials"] = new Mongo.Collection("MeteorToysCredentials");
}