// Start by observing 
Meteor.startup(function(){
  MeteorToys_ToyKit.hotKeys();
});

MeteorToys_ToyKit = {
  observe: function () {
    
    // first load 
    MeteorToys_ToyKit.runPubSub();
    MeteorToys_ToyKit.runJetSetter();
    
    // start
    if (!Object.observe) {
      setInterval(function(){
        MeteorToys_ToyKit.runPubSub();
        MeteorToys_ToyKit.runJetSetter();
      }, 3000);
    } else {
      Object.observe(Meteor.default_connection._subscriptions, function() {
        MeteorToys_ToyKit.runPubSub();
      })
      Object.observe(Session.keys, function () {
        MeteorToys_ToyKit.runJetSetter();
      })
    }
    // end
  },
  hotKeys: function () {
    // start
    $(document).keydown(function (e) {
      if (e.keyCode === 77 && e.ctrlKey) {
        MeteorToys_ToyKit.toggleDisplay();
      }
    });
    // end
  },
  toggleDisplay: function () {

    var displayStatus = Session.get("MeteorToys_display");

    // Start Toy Processes
    // Break this out into seperate function later
    if (typeof displayStatus  === 'undefined') {
      MeteorToys_ToyKit.observe();
      Tracker.autorun(function () {
        Meteor.subscribe("MeteorToys", Session.get("MeteorToys_autopublish"));
      });
    }

    if (displayStatus) {
      Session.set("MeteorToys_display", false);
    } else {
      Session.set("MeteorToys_display", true);
    }

  },
  runJetSetter: function () {
    if (Package["msavin:jetsetter"]) {
      Package["msavin:jetsetter"].JetSetter.getKeys()
    }
  },
  runPubSub: function () {
    if (Package["msavin:sub"] || Package["msavin:mongol"]) {
      
      var subscriptions  = Meteor.default_connection._subscriptions,
          subKeys        = Object.keys(subscriptions);

      Session.set("MeteorToys_PubSub", subKeys);

    }
  }
}


// Public Function 

MeteorToys_Parse = function (data) {
  var newObject = false;

  try {
    newObject = JSON.parse(data);
    
  } catch (error) {
    newObject = String(data)
  }

  return newObject;
}

